import { BrowserRouter, Routes, Route } from "react-router-dom";
import { useState } from "react";
import Home from "./Home";
import Shop from "./Shop";
import Product from "./Product"

const Routing = () => {
  const [cartItems, setCartItems] = useState([]);

  const addToCart = (productid, productList) => {
      let newProduct = productList[productid];
      newProduct.quantity = 1;
      let isAdded = false
      setCartItems(prevItems => {
        prevItems.forEach(item => {
          if (item === newProduct) {
            isAdded = true;
          }
        });
        if (!isAdded) {
          return [...prevItems, newProduct];
        } else {
          return prevItems;
        }
      })
  }

  return (
    <BrowserRouter basename="/shopping-cart">
      <Routes>
        <Route path="/" element={<Home items={cartItems} />} />
        <Route path="/catalog" element={<Shop items={cartItems} addItems={setCartItems}/>} />
        <Route path="/catalog/:productId" element={<Product items={cartItems} addItem={addToCart}/>} />
      </Routes>
    </BrowserRouter>
  );
};

export default Routing;