const Card = (props) => {
    return (
        <div className="card">
            <div className="cover">
                <img src={props.image} alt="Watch" />
            </div>
            <div className="description">
                <h3>{props.description}</h3>
                <h4>Ksh. {props.price}</h4>
            </div>
        </div>
    )
}

export default Card;