import CartCard from "./CartCard";
import { useState, useEffect } from "react";
const uniqid = require("uniqid");
const Cart = (props) => {

    const [total, setTotal] = useState(0);
    const checkout = () => {
        alert(`Thank you for placing an order with us. Amount payable Ksh ${total}`)
    }
    const hideCart = () => {
        const cartPage = document.querySelector(".cart-main");
        cartPage.classList.remove("show")
    }
    const sumTotal = () => {
        if (props.cart.length === 1) {
            setTotal(props.cart[0].price)
        } else if (props.cart.length > 1) {
            const reducer = (accumulator, curr) => accumulator.price + (curr.price*curr.quantity);
            let sum = props.cart.reduce(reducer);
            setTotal(prevVal => {
                return prevVal =+ sum
            })
        }
    }
    
    useEffect(() => {sumTotal()}, [props.cart])
    
    return (
        <div className="cart-main">
            <section className="cart-section">
                <h1>Your Shopping Cart</h1>
                <div className="cart-items">
                    {
                        props.cart.length > 0 ? 
                        props.cart.map((item) => {return <CartCard key={uniqid()} product={item} addTotal={setTotal}/>})
                        : <p className="no-itmes">Cart is Empty</p>
                    }
                </div>
                <div className="cart-actions">
                    <h3 className="total">Total: Ksh: {total}</h3>
                    <button onClick={checkout} className="checkout">Checkout</button>
                    <button onClick={hideCart} className="close">Close</button>
                </div>
            </section>
        </div>
    )
}

export default Cart;