import { useState } from "react";


const CartCard = (props) => {
    const [quantity, setQuantity] = useState(1);
    const handleChange = (e) => {
        let val = e.target.value;
        setQuantity(val)
        props.product.quantity = quantity;
    }
    const addPrice = () => {
        let price = quantity*props.product.price;
        props.addTotal(prev => {
            return prev += price
        });
        
    }
    const subPrice = () => {
        let price = quantity*props.product.price;
        props.addTotal(prev => {
            if (prev > price) {
                return prev -= price
            } else {
                return props.product.price
            }
        });
    }
    const addQuantity = (e) => {
        e.preventDefault()
        setQuantity(Number(quantity)+1)
        addPrice();
    }
    const removeQuantity = (e) => {
        e.preventDefault()
        if (quantity > 1) {
            setQuantity(quantity-1)
        } else {
            setQuantity(quantity)
        }
        subPrice()
    }
    return (
        <div className="item">
            <div className="item-img">
                <img src={props.product.image} alt="watch" /> 
            </div>
            <div className="item-description">
                <h3>{props.product.description.substring(0,25)}</h3>
                <p>Ksh {props.product.price}</p>
                <form className="change-items" >
                    <label htmlFor="quantity">Quantity</label> <br />
                    <div className="adjust">
                        <button onClick={removeQuantity}>-</button>
                        <input onChange={handleChange} id="quantity" type="number" value={quantity} />
                        <button onClick={addQuantity}>+</button>
                    </div>
            </form>
            </div>
        </div>
    )
}
export default CartCard;